import React from 'react'
import { Table } from 'react-bootstrap'

const HistoryTable = (props) => {
    var result = [];
    var i = props.history.length - 1
    if(i >= 0) {
        while(true) {
            var temp = {...props.history[i]}
            temp.index = i + 1
            result.push({...temp})
            i--;
            if(i < 0 || i === props.history.length - 6) {
                break
            }
        }
    }

    return (
        <div className='display-table'>
            <div className="table-board">
                <Table style={{ width: '100%' }}>
                    <thead>
                        <tr>
                            <th style={{ width: '3%' }}>#</th>
                            <th style={{ width: '15%' }}>Name</th>
                            <th style={{ width: '24%' }}>Wallet Address</th>
                            <th style={{ width: '14%' }}>Telegram</th>
                            <th style={{ width: '13%' }}>Twitter</th>
                            <th style={{ width: '9%' }}>Amount</th>
                            <th style={{ width: '20%' }}>Txn</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.history.length > 0 ? 
                            result.map((transaction) => {
                                var wallet = transaction.wallet_address;
                                wallet = wallet.slice(0, 11) + " ..... " + wallet.slice(37);
                                var txn = transaction.txn;
                                txn = txn.slice(0, 11) + " ..... " + txn.slice(62);
                                return (
                                    <tr>
                                        <td>{transaction.index}</td>
                                        <td>{transaction.name}</td>
                                        <td>{wallet}</td>
                                        <td>{transaction.telegram}</td>
                                        <td>{transaction.twitter}</td>
                                        <td>{transaction.amount}</td>
                                        <td>{txn}</td>
                                    </tr>
                                )
                            }) : null
                        }
                    </tbody>
                </Table>
            </div>
        </div>
    )   
}

export default HistoryTable;